.CloseButton{background-color:#ffb80f !important;border-radius:0px;border:none;padding:5px}.CloseButton-icon{width:40px;height:40px;cursor:pointer;-webkit-tap-highlight-color:transparent;-webkit-transition:-webkit-transform 400ms;transition:-webkit-transform 400ms;-o-transition:transform 400ms;transition:transform 400ms;transition:transform 400ms, -webkit-transform 400ms;-webkit-user-select:none;-moz-user-select:none;-ms-user-select:none;user-select:none}.CloseButton-iconLine{fill:none;-webkit-transition:stroke-dasharray 400ms, stroke-dashoffset 400ms;-o-transition:stroke-dasharray 400ms, stroke-dashoffset 400ms;transition:stroke-dasharray 400ms, stroke-dashoffset 400ms;stroke:#fff;stroke-width:5.5;stroke-linecap:round}.CloseButton-iconLine--top{stroke-dasharray:40 160}.CloseButton-iconLine--middle{stroke-dasharray:40 142;-webkit-transform-origin:50%;-ms-transform-origin:50%;transform-origin:50%;-webkit-transition:-webkit-transform 400ms;transition:-webkit-transform 400ms;-o-transition:transform 400ms;transition:transform 400ms;transition:transform 400ms, -webkit-transform 400ms}.CloseButton-iconLine--bottom{stroke-dasharray:40 85;-webkit-transform-origin:50%;-ms-transform-origin:50%;transform-origin:50%;-webkit-transition:stroke-dashoffset 400ms, -webkit-transform 400ms;transition:stroke-dashoffset 400ms, -webkit-transform 400ms;-o-transition:transform 400ms, stroke-dashoffset 400ms;transition:transform 400ms, stroke-dashoffset 400ms;transition:transform 400ms, stroke-dashoffset 400ms, -webkit-transform 400ms}.CloseButton.is-active .CloseButton-icon--rotate{-webkit-transform:rotate(45deg) scale(1.4);-ms-transform:rotate(45deg) scale(1.4);transform:rotate(45deg) scale(1.4)}.CloseButton-iconLine--top{stroke-dashoffset:-64px}.CloseButton-iconLine--middle{-webkit-transform:rotate(90deg);-ms-transform:rotate(90deg);transform:rotate(90deg)}.CloseButton-iconLine--bottom{stroke-dashoffset:-64px}
