.BlueArrowButton{position:relative;-webkit-transition:opacity 0.3s ease-out;-o-transition:opacity 0.3s ease-out;transition:opacity 0.3s ease-out;cursor:pointer}.BlueArrowButton path{-webkit-transition:fill 0.3s ease-out;-o-transition:fill 0.3s ease-out;transition:fill 0.3s ease-out}.BlueArrowButton.is-disabled{opacity:0;cursor:default}.BlueArrowButton.is-disabled path{fill:rgba(94,97,104,0.53) !important}
