.AsideButton{width:20px;height:83px;background-color:#4a2d87;border-radius:0 0 0 0;-webkit-box-shadow:7px 0 5px 0 rgba(0,0,0,0.16);box-shadow:7px 0 5px 0 rgba(0,0,0,0.16);border:none;padding:5px}.AsideButton-icon{right:2px;position:relative;-webkit-transition:all 400ms;-o-transition:all 400ms;transition:all 400ms;margin-left:1px}.AsideButton-iconPath{position:relative;cursor:pointer;-webkit-tap-highlight-color:transparent;-webkit-transition:opacity 400ms, -webkit-transform 400ms;transition:opacity 400ms, -webkit-transform 400ms;-o-transition:transform 400ms, opacity 400ms;transition:transform 400ms, opacity 400ms;transition:transform 400ms, opacity 400ms, -webkit-transform 400ms;-webkit-user-select:none;-moz-user-select:none;-ms-user-select:none;user-select:none;fill:#fff;fill-rule:evenodd;opacity:0.398}.AsideButton:hover .AsideButton-iconPath{opacity:1}.AsideButton.is-active .AsideButton-icon{right:1px;-webkit-transform:scaleX(-1);-ms-transform:scaleX(-1);transform:scaleX(-1);-webkit-filter:FlipH;filter:FlipH;-ms-filter:"FlipH"}
