.Loading{position:relative;width:100px;height:100px}.Loading .bg{width:100%;height:100%;background:#302758;border-radius:50%;opacity:0.54}.Loading svg{top:14px;left:14px;position:absolute;width:calc(100% - 28px);height:calc(100% - 28px);display:block;overflow:visible}.Loading svg circle{fill:none;stroke:#ffb80f;stroke-width:4px;stroke-linecap:butt}.Loading svg .circle{stroke:#ffb80f;stroke-width:17px;stroke-dashoffset:0;stroke-dasharray:566 566;-webkit-animation:LoadingRotate 2.5s linear infinite;animation:LoadingRotate 2.5s linear infinite}.is-mobile .Loading svg{width:calc(100% - 14px);height:calc(100% - 14px);top:7px;left:7px}.is-mobile .Loading{width:50px;height:50px}@-webkit-keyframes LoadingRotate{0%{-webkit-transform:rotate(0deg);transform:rotate(0deg);stroke-dasharray:10 566}50%{-webkit-transform:rotate(360deg);transform:rotate(360deg);stroke-dasharray:566 566;stroke-dashoffset:0}100%{-webkit-transform:rotate(1080deg);transform:rotate(1080deg);stroke-dasharray:10 566}}@keyframes LoadingRotate{0%{-webkit-transform:rotate(0deg);transform:rotate(0deg);stroke-dasharray:10 566}50%{-webkit-transform:rotate(360deg);transform:rotate(360deg);stroke-dasharray:566 566;stroke-dashoffset:0}100%{-webkit-transform:rotate(1080deg);transform:rotate(1080deg);stroke-dasharray:10 566}}
