.ArrowButton{position:relative;-webkit-transition:opacity 0.3s ease-out;-o-transition:opacity 0.3s ease-out;transition:opacity 0.3s ease-out;cursor:pointer}.ArrowButton path{-webkit-transition:stroke 0.3s ease-out;-o-transition:stroke 0.3s ease-out;transition:stroke 0.3s ease-out}.ArrowButton:hover path{stroke:#2ab4c7 !important}.ArrowButton.is-disabled{opacity:0.8;cursor:default}.ArrowButton.is-disabled path{stroke:rgba(94,97,104,0.53) !important}
